import React, { useState, FunctionComponent } from 'react';
import './css/Buttons.css';
import { Segment } from './models/Segment';

interface ButtonsProps {
    meaningData: Array<Segment>;
    setNewMeaningData: (segments: Array<Segment>) => void;
}

export default function Buttons({ meaningData, setNewMeaningData }: ButtonsProps) {

    let clicked = Array(meaningData.length).fill(false);

    const buttons = meaningData.map((segment: Segment, index: number) => {

        const ref = React.createRef<HTMLButtonElement>();

        if (segment.meaning == null) {
            return (
                <button ref={ref} key={index + segment.word} className="record-button" onClick={() => ClickTheSegment(ref.current, index)}>
                    {segment.word}
                </button>
            );
        }
        else {
            return (
                <button ref={ref} key={index + segment.word} className="record-button-done" onClick={() => ClickTheDoneSegment(ref.current, index)} onMouseOver={() => setValueForButton(ref.current, segment.meaning)} onMouseOut={() => setValueForButton(ref.current, segment.word)}>
                    {segment.word}
                </button>
            );
        }
    });

    return (
        <div className="Buttons">
            {buttons}
            <input type='text' id='meaning'></input>
            <button onClick={() => { MeaningButtonClicked(); }}>Wprowadz</button>
            <button onClick={() => { saveMeaningData(meaningData); }}>Zapis</button>
            Nazwa pliku: <input type='text' id='fileName'></input>
        </div>
    );

    function ClickTheSegment(button: HTMLButtonElement | null, index: number) {
        if (button == null) {
            return;
        }

        if (clicked[index]) {
            clicked[index] = false;
        }
        else {
            clicked[index] = true;
        }

        button.classList.toggle('active');
    }

    function ClickTheDoneSegment(button: HTMLButtonElement | null, index: number) {
        if (button == null) {
            return;
        }

        if (clicked[index]) {
            clicked[index] = false;
        }
        else {
            clicked[index] = true;
        }

        button.classList.toggle('active');
    }

    function MeaningButtonClicked() {
        // gdy scalani zostawić stare meaning
        const meaning = (document.getElementById("meaning") as HTMLButtonElement).value;

        let newMeaningData = [];

        let firstIndex = undefined;
        let words = "";

        for (let i = 0; i < clicked.length; ++i) {
            if (clicked[i]) {
                if (firstIndex === undefined) {
                    firstIndex = i;

                    for (var k = i; k < clicked.length; ++k) {
                        if (!clicked[k]) {
                            break;
                        }
                        words += " " + meaningData[k].word;
                    }
                    words = words.slice(1);
                    newMeaningData.push(new Segment(words, meaning));
                    i = k;
                }
                else {
                    newMeaningData.push(meaningData[i]);
                }
            }
            else {
                newMeaningData.push(meaningData[i]);
            }
        }

        setNewMeaningData(newMeaningData);
    }
}

function setValueForButton(button: HTMLButtonElement | null, value: string | null) {
    if(button == null || value == null)
    {
        return;
    }
    button.innerHTML = value;
    button.classList.toggle('onmouse');
}

function saveMeaningData(data: Array<Segment>) {
    let fileName = (document.getElementById("fileName") as HTMLInputElement).value;

    let jsonData = JSON.stringify(data);

    const GitHub = require("github-api");

    const github = new GitHub({
        token: "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",
    });

    const owner = "miandrop";
    const repoName = "data";
    const branch = 'main';
    const filePath = 'files/translations/' + fileName + '.json';

    const repository = github.getRepo(owner, repoName);

    repository.writeFile(branch, filePath, jsonData, 'Zapis pliku JSON', {})
        .then(() => {
            console.log('Plik JSON został zapisany pomyślnie.');
        })
        .catch((error: Error) => {
            console.error('Wystąpił błąd podczas zapisu pliku JSON:', error);
        });
}