import FileBrowser from './FileBrowser';
import Record from './Record';
import View from './View';
import { useState } from 'react';
import { Segment } from './models/Segment';

export default function Hub() 
{
    const [fileToView, setFileToView] = useState<Array<Segment> | null>(null);
    const [addNewFile, setAddNewFile] = useState<boolean>(false);

    if (fileToView != null) {
        return (
            <div className="hub">
                <View meaningData={fileToView} goReturn={returnFromView} ></View>
            </div>
        );
    }

    if (addNewFile == true) {
        return (
            <div className="hub">
                <Record goReturn={returnFromRecord}></Record>
            </div>
        );
    }

    return (
        
        <div className="hub">
            <FileBrowser setFile={setStateForFile} />
            <div>
                <button onClick={() => { setStateForAddNewFile() }}>Nowy plik</button>
            </div>
        </div>
    );

    function setStateForFile(fileData : Array<Segment>) {
        setFileToView(fileData);
    }

    function setStateForAddNewFile() {
        console.log("zmiana stanu");
        setAddNewFile(true);
    }

    function returnFromView() {
        setFileToView(null);
    }

    function returnFromRecord() {
        setAddNewFile(false);
    }
}